body {
  padding: 0;
  margin: 0 0;
  font-family: sans-serif;
}
img{
  width: 200px;
  height: 100px;
}
.navbar li {
    display: inline-block;
    text-align: center;
  }
  
  .itemLeft {
    width: 15%;
  }
  
  .itemCenter {
      width: 70%;
  }
  
  .itemRight {
    width: 15%;
  }
  
  .navbar {
    background:plum;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    padding: 5px;
    
  }
  
  li {
    color: black;
    padding: 15px 0;
  }
  
  body {
    font-size: 30px;
  }
.header{
	background-color:plum;
	position: relative;
	width: 100%;
	padding: 1px;

}

h1{
	text-align: center;
	font-size: 40px;
	font-weight: bold;
	padding: 10px 0 20px 0; 
}

h2{
	text-align: center;
	font-size: 20px;
	padding-bottom: 5px;
}
